/* pulse in SVG */
svg.pulse-svg {
	overflow: visible;
}
svg.pulse-svg .first-circle,
svg.pulse-svg .second-circle,
svg.pulse-svg .third-circle {
	transform: scale(0.3);
	transform-origin: center center;
	animation: pulse-me 3s linear infinite;
	fill: var(--primary);
}
svg.pulse-svg .second-circle {
	animation-delay: 1s;
}
svg.pulse-svg .third-circle {
	animation-delay: 2s;
}

/* pulse in CSS */
.pulse-css {
	width: 1rem;
	height: 1rem;
	border-radius: 0.25rem;
	border-radius: 3.5rem;
	height: 1rem;
	position: absolute;
	background: var(--primary);
	right: 0px;
	top: 0px;
	width: 1rem;
}
.pulse-css:after,
.pulse-css:before {
	content: "";
	width: 1rem;
	height: 1rem;
	border-radius: 0.25rem;
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: -0.2rem;
	background-color: var(--primary);
	margin: auto;
	transform: scale(0.3);
	transform-origin: center center;
	animation: pulse-me 3s linear infinite;
}
[direction="rtl"] .pulse-css:after,
[direction="rtl"] .pulse-css:before {
	left: auto;
	right: -0.2rem;
}
@media only screen and (max-width: 1400px) {
	.pulse-css {
		height: 0.5rem;
		width: 0.5rem;
		top: 3px;
		right: 3px;
	}
}

@keyframes pulse-me {
	0% {
		transform: scale(0.3);
		opacity: 0;
	}
	50% {
		opacity: 0.1;
	}
	70% {
		opacity: 0.09;
	}
	100% {
		transform: scale(3);
		opacity: 0;
