}
.toggle-switch .custom-control-label:after {
	width: 24px;
	background: #959595;
	height: 24px;
	border-radius: 24px;
	top: -2px;
	left: -50px;
	transition: all 0.5s;
}
.toggle-switch .custom-control-label:before {
	border: 0;
	background: #d8d8d8;
	width: 40px;
	border-radius: 20px !important;
	height: 16px;
	left: -50px;
	transition: all 0.5s;
}
