	padding-left: 0;
	padding-right: 50px;
}
.toggle-switch.text-right .custom-control-label:after {
	left: auto;
	right: -30px;
}
.toggle-switch.text-right .custom-control-label:before {
	left: auto;
	right: -50px;
}
.toggle-switch.text-right
	.custom-control-input:checked
	~ .custom-control-label:after {
	left: auto;
	right: -43px;
}

