	background: var(--primary);
	display: block;
	height: 3px;
	border-radius: 3px;
	margin-top: 6px;
	margin-bottom: 6px;
	margin-left: auto;
	-webkit-transition: all 0.3s ease-in-out;
	transition: all 0.3s ease-in-out;
}

.hamburger .line:nth-child(1) {
	width: 20px;
