	color: #fff;
	position: relative;
}
.chatbox .msg_cotainer .msg_time {
	display: block;
	font-size: 11px;
	color: #fff;
	margin-top: 5px;
	opacity: 0.5;
}
.chatbox .msg_cotainer:after {
	content: "";
	position: absolute;
	left: -10px;
	border-right: 10px solid var(--primary);
	border-bottom: 10px solid transparent;
	border-top: 0px solid;
	top: 0;
}
.chatbox .msg_cotainer_send {
	background: #f4f6fa;
